import React from 'react';
import { ipcRenderer } from 'electron';
import { Text, TextInput, View } from 'react-native-electron';

process.env['ELECTRON_DISABLE_SECURITY_WARNINGS'] = 'true';


class Home extends React.Component {
  constructor(props) {
    super(props);
    this.showAlert = this.showAlert.bind(this);
    this.state = {text: ''};
  }

  componentDidMount = () => {
    this.setState({ loading: false })

    var sqlite3 = require('sqlite3').verbose();
    var db = new sqlite3.Database('eotdb.sqlite');
  }

  _handleClick = () => {
    var res = ipcRenderer.sendSync('get-result', { name: 'react' })
    alert(res);
  }
  showAlert() {
    alert("Hello world!");
  }

  render() {
    return (
      <div className="App">
      <script src="http://localhost:35729/livereload.js"></script>
      <View style={{padding: 10}}>
        <TextInput
          style={{height: 40, width: "10vh"}}
          placeholder="Type here to translate!"
          onChangeText={(text) => this.setState({text})}
          value={this.state.text}
        />
        <Text style={{padding: 10, fontSize: 42}}>
          {this.state.text.split(' ').map((word) => word && 'üçï').join(' ')}
        </Text>
      </View>
        <header className="App-header">
          <button onClick={this.showAlert}>Click me ok!</button>
        </header>
      </div>
      );
  }
}

export default Home;